@import "main";

#dashboard {
	background: $gray;
	height: 100vh;
	width: 100%;
	padding: 0;
	margin: 0;
}
#dataresult {
	position: absolute;
	float: left;
	width: 72%;
	height: 100vh;
	padding: 4%;
	border-right: 1px solid rgba($darkgray, 0.5);
}
#nameList {
	position: absolute;
	right: 0;
	width: 27%;
	padding: 4%;
}
#listButtons {
	position: relative;
	left: 0;
	background: rgba($white, 1);
}
.nameToPdf {
	background: rgba($white, 0.3);
	padding: 5px;
	color: $clickable;
	margin: 1px 0;
	transition-duration: 0.5s;
}
.nameToPdf:hover {
	background: rgba($white, 1);
}
.nameTabs {
	@extend .nameToPdf;
	display: inline-block;
	margin: 1px;
}
#printView {
	width: 100%;
	height: 75vh;
	background: white;
	border-right: 1px solid rgba($darkgray, 0.3);
	padding: 40px;
	overflow-y: scroll;
	h4 {
		margin-top: 30px;
		border: 1px solid rgba($darkgray, 0.3);
		background: rgba($darkgray, 0.1);
		padding: 10px;
	}
	.subTitle {
		text-align: left;
		font-size: 16px;
		margin-top: 20px;
		text-transform: capitalize;
	}
}
.resultToPrint {
	width: 100%;
	margin: 0;
	text-align: left;
	border-bottom: 1px solid rgba($darkgray, 0.3);
	display: block;
	padding: 10px 0;

	h5,
	p {
		display: inline-block;
		margin: 7px 5px;
		text-align: left;
		font-weight: 400;
	}
	h5 span {
		color: $clickable;
		font-weight: 500;
		border: 2px solid $tomato;
		padding: 7px;
		min-width: 24px;
		width: auto;
	}
	p {
		padding-right: 20px;
	}
	.numb {
		display: inline-block;
		width: 3%;
	}
	.questions {
		display: inline-block;
		width: 58%;
	}
	.opt {
		display: inline-block;
		width: 28%;
		padding-left: 10px;
		padding-right: 10px;
	}
	.answers {
		display: inline-block;
		width: 34%;
		border: 1px solid rgba($darkgray, 0.3);
		h5 {
			width: 32px;
		}
		h5 span {
			width: 32px;
			border-radius: 50%;
			padding: 2px 5px;
		}
	}
}
#downloads {
	background: $clickable;
	color: $white;
	position: fixed;
	bottom: 0;
	margin: 0 auto;
	margin-bottom: 20px;
	padding: 10px;
	border: none;
}
